import React, { Suspense } from 'react';

import {
  Route,
  Switch,
  Header,
  Home,
  LoginPage,
  RegisterPage,
  ErrorPage,
  Detail,
  SearchResults,
  GifsContextProvider,
  UserContextProvider,
  homePath
} from 'util/imports';

import 'styles.css';

export default function App() {
  return (
    <UserContextProvider>
      <div className="App">
        <Suspense fallback={null}>
          <section className="App-content">
            <Header />
            <GifsContextProvider>
              <Switch>
                <Route path={`${homePath}`} component={Home} />
                <Route
                  path={`${homePath}/search/:keyword/:rating?`}
                  component={SearchResults}
                />
                <Route path={`${homePath}/gif/:id`} component={Detail} />
                <Route path={`${homePath}/login`} component={LoginPage} />
                <Route path={`${homePath}/register`} component={RegisterPage} />
                <Route path={`${homePath}/:rest*`} component={ErrorPage} />
              </Switch>
            </GifsContextProvider>
          </section>
        </Suspense>
      </div>
    </UserContextProvider>
  );
}
